body{z-index:1}.title,.maintitle,.subtitle{z-index:3;position:absolute;background-color:rgba(34,34,34,0.664);color:#fff;font-family:"Comfortaa", cursive;-webkit-animation-name:titleAppears;animation-name:titleAppears;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:1;animation-iteration-count:1;border-radius:10px;padding:0.5rem 0.5rem}@-webkit-keyframes titleAppears{from{opacity:0;-webkit-transform:translateY(3rem);transform:translateY(3rem)}to{opacity:1;-webkit-transform:translateY(0rem);transform:translateY(0rem)}}@keyframes titleAppears{from{opacity:0;-webkit-transform:translateY(3rem);transform:translateY(3rem)}to{opacity:1;-webkit-transform:translateY(0rem);transform:translateY(0rem)}}.maintitle{font-size:3rem;top:50%;left:5%}.subtitle{font-size:2rem;top:73%;left:25%}.img{z-index:2;position:absolute;width:100vw;margin:0px}#entry-scroller{font-size:68px;color:rgba(221,223,224,0.274);z-index:5;top:87%;left:45%;position:absolute;-webkit-animation-name:blink;animation-name:blink;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}#entry-scroller:hover{color:rgba(255,255,255,0.486);font-size:72px}@-webkit-keyframes blink{from{color:rgba(148,149,150,0.274)}to{color:rgba(253,253,253,0.637)}}@keyframes blink{from{color:rgba(148,149,150,0.274)}to{color:rgba(253,253,253,0.637)}}.row,.container-fluid{margin-left:0px !important;margin-right:0px !important;padding-left:0px !important;padding-left:0px !important}@media (max-width: 570px){.title,.maintitle,.subtitle{font-size:1.5rem;top:50%;left:0%;padding:0.4rem 0.2rem}.subtitle{font-size:1.2rem;top:60%;left:0%;padding:0.4rem 0.2rem}legend{font-size:1.5rem}}@media (min-width: 570px){.maintitle{font-size:1.5rem;top:50%;left:0%}.subtitle{font-size:1.2rem;top:60%;left:10%}#entry-scroller{font-size:56px;color:rgba(221,223,224,0.274);z-index:5;top:87%}#entry-scroller:hover{color:rgba(255,255,255,0.486);font-size:60px}}@media (min-width: 800px){.maintitle{font-size:2rem;top:55%}.subtitle{font-size:1.5rem;top:63%;left:15%}}@media (min-width: 1000px){.title,.maintitle,.subtitle{font-size:3rem;top:63%}.subtitle{font-size:2rem;top:80%;left:25%}}

